.searchBar-gallery {
  margin: 0.5rem auto;
  padding: 0 2rem;
  width: 88%;
  height: 45px;
  background: #ffffff;
  border: 1px solid #000000;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  display: flex;
  justify-content: space-between ;
  align-items: center;
}

.searchBar__h2-select {
  display: flex;
  align-items: center;
  gap: 2rem;
}

.h2-title-gallery {
  font-style: normal;
  font-weight: 900;
  color: #000000;
}

.select {
  font-weight: 900;
  font-size: 18px;
  line-height: 27px;
  text-decoration-line: underline;
  color: #6f8f72;
  border: none;
}

.searchBar__seachContainer-icon {
  background: #d9d9d9;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 350px;
  height: 32px;
  border-radius: 50px;
  padding: 0 1rem;
  gap: 1rem;
}

.searchBar__searchContainer {
  width: 100%;
  background: none;
  color: black;
  font-size: 1.2rem;
  letter-spacing: .1rem;
  border: none;
  display: flex;
  gap:.8rem
}

.search-container__input {
  background: none;
  border: transparent;
  color: black;
}

.search-input:focus {
  outline: none;
}


.search-input {
  width: 100%;
  background: none;
  color: black;
  font-size: 1.2rem;
  letter-spacing: .1rem;
  border: none;
}


.main-content {
  /* min-height: 100vh; */
  position: relative;
  width: 90%;
  padding: 1rem 0 2rem 0;
  margin: auto;
}

.modal{
  position: absolute;
  bottom: 0;
  right: 0;
  z-index: 10;
  background: transparent;
}

.main-content__grid {
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(auto-fill, 360px);
  gap: 1rem;
}

.grid-img {
  height: 500px;
  width: 100%;
  max-width: 100%;
  aspect-ratio: 1;
  object-fit: cover;
  object-position: center center;
  border-radius: 5px;
  transition: all 0.2s ease-in-out;
  z-index: 2;
}

.grid-img:hover {
  border: 1.5px solid white;
}

@media (max-width: 1200px) {

  .searchBar__seachContainer-icon {
    width: 28%;
  }

}

@media (max-width: 1000px) {
  .searchBar__seachContainer-icon {
    width: 300px;
  }
}

@media (max-width: 840px) {
  .searchBar {
    margin: 0 auto;
    width: 60%;
  }
  .searchBar__seachContainer-icon {
    width: 275px;
    margin: 1rem;
  }

  h2 {
    font-size: 1rem;
  }

  .main-content {
    width: 100%;
    padding: 0;
    margin: 0 auto;
  } 

}

@media (max-width: 650px) {
  .searchBar__seachContainer-icon {
    width: 250px;
  }

  .search-input {
    width: 100%;
    background: none;
    color: black;
    font-size: 1rem;
    letter-spacing: 1px;
  }

  .h2-title-gallery {
    display: none;
  }
}

@media (max-width: 450px) {

  .searchBar-gallery {
    padding: 0 .5rem;
  }

  .select {
    font-size: 15px;
    line-height: 20px;
  }

  .searchBar__seachContainer-icon {
    width: 50%;
  }

  .main-content__grid {
    display: grid;
    justify-content: center;
    grid-template-columns: repeat(auto-fill, 1fr);
    gap: 1rem;
  }

  .grid-img {
    height: 300px;
  }

  .search-input {
    width: 100%;
    background: none;
    color: black;
    font-size: 1rem;
    letter-spacing: none;
  }
  
}